import React, { useEffect, useState } from "react";
import "./App.css";
import {
  ThemeProvider,
  createTheme,
  TextField,
  Typography,
  Box,
} from "@mui/material";
import parse from "./thelanguage/parser";
import transpile from "./thelanguage/transpiler";
import { dark } from "@mui/material/styles/createPalette";

const theme = createTheme({
  palette: dark,
});

function App() {
  const [src, setSrc] = useState("");

  function ResultBox() {
    const [result, setResult] = useState("");

    useEffect(() => {
      compile();
    });

    async function compile() {
      try {
        setResult(transpile(parse(src).tokens, src).src.join("\n"));
      } catch (e) {
        return "Compilation error";
      }
    }

    return (
      <TextField
        dir="rtl"
        id="outlined-textarea"
        fullWidth
        multiline
        value={result}
        rows={20}
      />
    );
  }

  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <Typography variant="h2" align="center" color="white">
          
        </Typography>
        <Box sx={{ display: "flex" }}>
          <ResultBox />
          <TextField
            dir="rtl"
            label=""
            placeholder=" !"
            rows={20}
            fullWidth
            multiline
            onChange={(e) => {
              setSrc(e.target.value);
            }}
          >
            {src}
          </TextField>
        </Box>
      </div>
      <Typography align="center" dir="rtl" color="white">
              ,     
            .       
              .
      </Typography>
      <Typography variant="h3" align="center" color="white">
           <b></b> 
      </Typography>
      <Typography
        dir="rtl"
        borderColor="black"
        margin={3}
        border={3}
        variant="h3"
        align="center"
        color="white"
      >
         " !"
      </Typography>

      <Typography
        textAlign={"center"}
        variant="h3"
        align="center"
        color="white"
      >
           
      </Typography>
      <Typography
        dir="rtl"
        textAlign={"center"}
        borderColor="black"
        margin={3}
        border={3}
        variant="h3"
        align="center"
        color="white"
      >
          = "!"
      </Typography>
    </ThemeProvider>
  );
}

export default App;
