import { Message, Token, TokenType } from "./common";
import { isDigit } from "./util/parser";

export default function parse(input: string) {
  let length = input.length;

  let line = 0;
  let start = 0;
  let idx = 0;

  const isAtEnd = () => !(idx < length);
  const slashZeroIfNull = (val: string) => (val ? val : "\0");

  const current = () => slashZeroIfNull(input[idx]);
  const next = () => slashZeroIfNull(input[idx++]);
  const peek = () => slashZeroIfNull(input[idx + 1]);

  const tokens: Array<Token> = [];
  const messages: Array<Message> = [];

  const token = (type: TokenType) =>
    tokens.push({ type: type, line: line, start: start, length: idx - start });

  function pushError(message: string) {
    messages.push({
      source: "parser",
      type: "error",
      message: message,
      state: { line: line, start: start, length: idx - start },
    });
    return false;
  }

  function checkAndPush(rest: string, type: TokenType) {
    for (let index = 0; index < rest.length; index++) {
      next();
      if (rest[index] !== current()) {
        // backtrack
        idx -= index + 1;
        return false;
      }
    }
    next();
    token(type);
    return true;
  }

  function parseToken() {
    while (idx < length) {
      eatWhitespace();

      start = idx;

      if (isDigit(current())) {
        return Digit();
      }

      switch (current()) {
        case "=":
          return equal();
        case "(":
          return parenRight();
        case ")":
          return parenLeft();
        case ",":
          return comma();
        case '"':
          return doubleQuote();
      }

      if (!keyWord()) {
        return Identifier();
      }
    }

    function eatWhitespace() {
      while (true) {
        switch (current()) {
          case " ":
          case "\r":
          case "\t":
            next();
            break;

          case "\n":
            line++;
            next();
            break;

          case "/":
            if (peek() === "/") {
              // A comment goes until the end of the line.
              while (peek() !== "\n" && !isAtEnd()) next();
            } else {
              return;
            }
            break;

          default:
            return;
        }
      }
    }

    function isDestructive(character: string) {
      return (
        character === "\n" ||
        character === "\t" ||
        character === "\r" ||
        character === " " ||
        character === "\0"
      );
    }

    function Identifier() {
      next();

      let oncoming = next();
      while (!isDestructive(oncoming)) {
        oncoming = next();
      }

      idx--;
      token(TokenType.Identifier);
    }

    function equal() {
      token(TokenType.Equal);
      next();
    }

    // '"'
    function doubleQuote() {
      next();
      while (next() !== '"') {
        if (isAtEnd()) {
          pushError("Unterminated String");
          return;
        }
      }
      token(TokenType.String);

      next();
    }

    function Digit() {
      let hasDecimalPoint = false;
      const isDecimalPoint = () => {
        if (hasDecimalPoint) {
          return false;
        }

        if (current() === ",") {
          hasDecimalPoint = true;
          return hasDecimalPoint;
        }

        return false;
      };

      while (isDigit(current()) || isDecimalPoint()) {
        next();
      }

      token(TokenType.Number);
    }

    // ','
    function comma() {
      token(TokenType.Comma);
      next();
    }

    // '('
    function parenLeft() {
      token(TokenType.OpenParen);
      next();
    }

    // ')'
    function parenRight() {
      token(TokenType.CloseParen);
      next();
    }

    function keyWord() {
      return (
        hamza() ||
        upHamzaAleph() ||
        downHamzaAleph() ||
        aleph() ||
        baa() ||
        taa() ||
        thaa() ||
        jeem() ||
        haa() ||
        khaa() ||
        dal() ||
        raa() ||
        zay() ||
        seen() ||
        sheen() ||
        sad() ||
        dadd() ||
        ttaa() ||
        zaa() ||
        ain() ||
        ghain() ||
        faa() ||
        kaff() ||
        keff() ||
        lam() ||
        meem() ||
        noon() ||
        haah() ||
        waw() ||
        yaa()
      );
    }

    // ''
    function hamza() {
      return false;
    }

    // ''
    function upHamzaAleph() {
      return false;
    }

    // ''
    function downHamzaAleph() {
      if (checkAndPush(" ", TokenType.Or)) return true;
      else return false;
    }

    // ''
    function aleph() {
      if (checkAndPush(" ", TokenType.Or)) return true;
      else if (checkAndPush(" ", TokenType.End)) return true;
      else if (checkAndPush(" ", TokenType.Print)) return true;
      else return false;
    }

    // ''
    function baa() {
      return false;
    }

    // ''
    function taa() {
      if (checkAndPush(" ", TokenType.Fun)) return true;
      else return false;
    }

    // ''
    function thaa() {
      return false;
    }

    // ''
    function jeem() {
      return false;
    }

    // ''
    function haa() {
      return false;
    }

    // ''
    function khaa() {
      if (checkAndPush(" ", TokenType.False)) return true;
      else return false;
    }

    // ''
    function dal() {
      return false;
    }

    // ''
    function raa() {
      return false;
    }

    // ''
    function zay() {
      return false;
    }

    // ''
    function seen() {
      return false;
    }

    // ''
    function sheen() {
      if (checkAndPush(" ", TokenType.Var)) return true;
      else return false;
    }

    // ''
    function sad() {
      if (checkAndPush(" ", TokenType.True)) return true;
      else return false;
    }

    // ''
    function dadd() {
      return false;
    }

    // ''
    function ttaa() {
      if (checkAndPush(" ", TokenType.While)) return true;
      else return false;
    }

    // ''
    function zaa() {
      return false;
    }

    // ''
    function ain() {
      return false;
    }

    // ''
    function ghain() {
      return false;
    }

    // ''
    function faa() {
      if (checkAndPush(" ", TokenType.Nil)) return true;
      else return false;
    }

    // ''
    function keff() {
      return false;
    }

    // ''
    function kaff() {
      return false;
    }

    // ''
    function lam() {
      if (checkAndPush(" ", TokenType.For)) return true;
      else return false;
    }

    // ''
    function meem() {
      return false;
    }

    // ''
    function noon() {
      if (checkAndPush(" ", TokenType.Class)) return true;
      else return false;
    }

    // ''
    function haah() {
      return false;
    }

    // ''
    function waw() {
      if (checkAndPush(" ", TokenType.And)) return true;
      else return false;
    }

    // ''
    function yaa() {
      return false;
    }
  }

  while (idx < length) {
    parseToken();
  }

  return {
    messages: messages,
    tokens: tokens,
  };
}
