export interface Message {
  source: string;
  type: string;
  message: string;
  state?: {};
}

export enum TokenType {
  // One or two character tokens.
  OpenParen, // 
  CloseParen, // 
  Comma, // 
  Bang,
  BangEqual,
  Equal, // 
  EqualEqual,
  Greater,
  GreaterEqual,
  Less,
  LessEqual,
  And, //  
  Or, //  

  // Literals.
  Identifier, // 
  JSLiteral,
  String, // 
  Number, // 

  Class, //  
  Else, //  
  False, //  
  Fun, //  
  For, //  
  If, //  
  Nil, //  
  Print, //  
  Return, //  
  True, //  
  Var, //  
  While, //  
  End, //  
}

export interface Token {
  type: TokenType;
  line: number;
  start: number;
  length: number;
}
